/* General styles */
body {
  font-family: 'Merriweather', serif;
  background-color: #fff;
  color: #000;
  margin: 0;
  padding: 0;
}

.mane > h1, h2, h3, h4, h5, h6 {
  color: #000!important;
  margin-bottom: 20px;
}

a {
  color: #FF76CE;
  text-decoration: none;
}

a:hover {
  color: #A3D8FF;
  text-decoration: underline;
}

/* Container styles */
.container {
  background-color: #FDFFC2;
  border: 1px solid var(--border-color);
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 8px;
}

/* Part styles */
.part {
  margin-bottom: 20px;
  border-radius: 8px;
}
.part > div{
  page-break-inside: avoid;
}
.part > table{
  background-color: #F6F0BC;
}
/* Warming section styles */
.warming {
  background-color: #94FFD8;
  padding: 15px;
  margin-bottom: 20px;
  border-radius: 8px;
  font-weight: bold;
}

/* Bunch styles */
.bunch {
  background-color: #FF76CE;
  padding: 15px;
  margin-bottom: 20px;
  border-radius: 8px;
}

/* Hint styles */
.hint {
  background-color: #FDFFC2;
  color: #181a20;
  padding: 10px;
  margin-bottom: 15px;
  border-left: 5px solid #FF76CE;
  border-radius: 4px;
  font-style: italic;
}

/* Expandable styles */
.expandable {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.expandable .word {
  font-weight: bold;
  text-decoration: underline;
  cursor: pointer;
}

.expandable .definition {
  display: none;
  position: absolute;
  background-color: #FF76CE;
  color: #fff;
  padding: 10px;
  border-radius: 4px;
  z-index: 1;
  width: max-content;
  max-width: 300px;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}

.expandable:hover .definition {
  display: block;
}

/* Styled lists */
ol, ul {
  margin-bottom: 20px;
}

li {
  margin: 1rem;
  font-size: 16px;
  line-height: 1.5;
}

li button {
  margin: 0 auto;
}

/* Table styles */
table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
  background-color: #A3D8FF;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}

th, td {
  padding: 15px;
  text-align: left;
  border-bottom: 1px solid var(--border-color);
}

th {
  background-color: #FF76CE;
  color: #fff;
}

td {
  background-color: #FDFFC2;
}

tr:last-child td {
  border-bottom: none;
}

/* Additional styles */
button {
  background-color: var(--primary-bg);
  color: var(--primary-text);
  border: 2px solid var(--border-color);
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: var(--hover-bg);
}

button:active {
  background-color: var(--active-bg);
}

/* Styled Lists */
ol, ul {
  width: 100%;
  color: var(--lesson-text);
}
li > div {
  display: flex;
  width: 100%;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
}
li > div > button{
  display: inline-block;
    min-width: 100px; /* Set a minimum width */
    width: auto; /* Allow the button to grow */
    max-width: 100%; /* Prevent it from exceeding the container width */
    cursor: pointer;
    text-align: center;
    box-sizing: border-box; /* Ensures padding is included in the width calculation */
    transition: background-color 0.3s ease, width 0.3s ease;

}
ol {
  
  padding-left: 2rem;
}

ul {
  list-style-type: disc;
  padding-left: 2rem;
}

/* Tables */
table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
  background-color: var(--lesson-bg);
  color: var(--lesson-text);
  border-radius: 8px;
  overflow: hidden;
}

table, th, td {
  border: 1px solid var(--lesson-border);
}

th, td {
  padding: 15px;
  text-align: left;
}

th {
  background-color: var(--lesson-accent1);
  color: var(--lesson-text);
}

td {
  background-color: var(--lesson-accent2);
}

tr:nth-child(even) td {
  background-color: var(--lesson-accent3);
}


.lesson-page {
  width: 100%;
  display: grid;
  grid-template-columns: 5% auto 5%;
  grid-template-rows: auto;
  overflow: hidden;
  position: absolute;
  gap:10px;
  background-color: #f8f9fa;

}

.lesson-page .content {
  grid-column: 2 / 3; /* Main content area */
  overflow: auto; /* Allows scrolling if content overflows */
  padding: 20px; /* Adjust padding as needed */
  height: 100%;
  box-sizing: border-box;
}

.lesson-page .content > div {
  max-width: 100%;
  word-wrap: break-word;
  margin: 0;
}
  
  .lesson-image {
    width: 100%;
    height: auto;
  }
  
  .lesson-content {
    margin-top: 20px;
  }
  .lesson-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8); /* Semi-transparent background */
    color: white;
    padding: 20px;
    box-sizing: border-box;
    overflow: auto;
    z-index: 1000; /* Ensure it's on top of other content */
  }
  
  .lesson-overlay img {
    max-width: 100%;
    height: auto;
  }
  
  .lesson-overlay button {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #f44336; /* Red color */
    border: none;
    color: white;
    padding: 10px 20px;
    cursor: pointer;
  }
  
  .lesson-overlay button:hover {
    background-color: #c62828; /* Darker red */
  }
  
  .correct {
    background-color: lightgreen !important;
  }
  .endpoint {
    max-width: 600px;
    margin: 50px auto;
    padding: 20px;
    background: linear-gradient(135deg, #f7f9fc, #e0e7ff);
    border-radius: 15px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.endpoint h2 {
    font-size: 28px;
    font-weight: bold;
    color: #333;
    margin-bottom: 20px;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    border-bottom: 2px solid #e0e7ff;
    padding-bottom: 10px;
}

.endpoint p {
    font-size: 18px;
    color: #555;
    margin: 15px 0;
    line-height: 1.6;
}

.endpoint button {
    background: linear-gradient(135deg, #6a8dff, #4a6aff);
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    padding: 12px 25px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.2s ease;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-transform: uppercase;
    letter-spacing: 1px;
}

.endpoint button:hover {
    background: linear-gradient(135deg, #4a6aff, #2a4aff);
    transform: translateY(-2px);
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
}

.endpoint button:active {
    transform: translateY(0);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.single-column {
  column-count: 1;
}
.fixed-button {
  position: fixed;
  left: 93%;
  top: 23%;
  z-index: 1000; /* Ensure it appears above other content */
  cursor: progress;
  display: flex;
  flex-direction: column;
  background: 0!important;
  gap: 1rem;
  box-shadow: none;
}
.tipper {
  position: absolute;
  top: -20px;
  opacity: 0;
  background-color: #ffe53b;
  background-image: linear-gradient(147deg, #ffe53b 0%, #ff2525 74%);
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition-duration: 0.2s;
  pointer-events: none;
  letter-spacing: 0.5px;
}

.tipper::before {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  background-color: #ff2525;
  background-size: 1000%;
  background-position: center;
  transform: rotate(45deg);
  bottom: -15%;
  transition-duration: 0.3s;
}
.fixed{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.151);
  position: relative;
}
.fixed img{
  box-shadow: none;
}
.fixed:hover .tipper {
  top: -40px;
  opacity: 1;
  transition-duration: 0.3s;
}

/* General styles for the tooltip overlay */
.tooltip-overlay {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.6); /* Darker background for clarity */
  display: flex;
  justify-content: center;
  z-index: 1000;
  padding: 10px;
}

/* Tooltip content styles */
.tooltip-content {
  background-color: #fff;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.3); /* Enhanced shadow for depth */
  width: 700px; /* Max width with responsive adjustment */
  max-height: 80vh; /* Constrain height */
  overflow-y: auto;
  column-count: 2; /* Vertical column layout */
  column-gap: 20px;
}

/* Header style */
.tooltip-content h3 {
  margin: 0 0 12px;
  font-size: 1.25rem;
  color: #333;
  text-align: center;
}

/* Close button styles */
.close-button {
  background-color: #ff4d4d;
  color: white;
  border: none;
  border-radius: 50%;
  padding: 8px;
  width: 32px;
  height: 32px;
  font-size: 0.7rem;
  line-height: 1;
  cursor: pointer;
  position: absolute;
  top: 12px;
  right: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease;
}

.close-button:hover {
  background-color: #e60000;
}

/* Style for the list of correct answers */
.tooltip-content ul,
.tooltip-content ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

.tooltip-content li {
  margin-bottom: 8px;
  font-size: 0.95rem;
  color: #444;
}

.tooltip-content li {
  position: relative;
  padding-left: 20px; /* Space for marker */
  margin-bottom: 8px;
  font-size: 0.95rem;
  color: #444;
}

.tooltip-content li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 8px;
  height: 8px;
  border-radius: 50%; /* Circle marker */
  background-color: #ff4d4d; /* Custom color for the marker */
}
/* To create a more "realistic" marker effect */
.highlight-realistic {
  background: linear-gradient(135deg, rgba(255, 255, 0, 0.6), rgba(255, 255, 0, 0.4));
  color: #333;
  font-weight: bold;
  padding: 0.2em;
  border-radius: 3px;
}
.writing-box{
  max-width: 600px; 
  margin: 0 auto; 
  padding: 20px; 
  background-color: #D4F6FF; 
  border-radius: 12px; 
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1); 
  transition: all 0.3s ease-in-out;
}
.letter{
  max-width: 800px; 
  margin: 30px auto; padding: 25px; 
  background-color: #ffffff; 
  border-left: 8px solid #fcd535; 
  border-radius: 10px; 
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1); 
  font-family: 'Merriweather', serif; 
  line-height: 1.6; 
  color: #333; 
  transition: all 0.3s ease;
}
.handwritten {
        font-family: 'Merriweather', serif; /* Keep Merriweather font */
        font-style: italic; /* Optionally make it italic for a handwritten effect */
        color: #333; /* Standard text color, no highlighting */
        font-weight: normal; /* Ensure it's not bold */
}
.letter-red{
  border-left: 8px solid #F5004F; 
}
.clickable{
  cursor: pointer
}

.yellow{
  background: linear-gradient(135deg, rgba(255, 255, 0, 0.6), rgba(255, 255, 0, 0.4));
}
textarea {
  width: 100%;
  max-width: 600px;
  padding: 1rem;
  font-size: 1rem;
  font-family: 'Open Sans', sans-serif;
  color: #333;
  background: linear-gradient(135deg, #f0f0f0, #ffffff);
  border: 2px solid transparent;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  outline: none;
  transition: all 0.3s ease-in-out;
  resize: none; /* Disable resizing of the textarea */
  overflow: hidden; /* Hide scrollbars */
  height: auto;
}

textarea:focus {
  background: #ffffff;
  border-color: #ff76ce; /* Highlight color */
  box-shadow: 0 4px 12px rgba(255, 118, 206, 0.4);
  transform: translateY(-2px);
}

textarea::placeholder {
  color: #aaa;
  font-style: italic;
  transition: color 0.3s ease;
}

textarea:focus::placeholder {
  color: #ff76ce;
}

textarea:hover {
  border-color: #fdffc2;
  box-shadow: 0 4px 10px rgba(253, 255, 194, 0.3);
}

textarea:disabled {
  background: #eaeaea;
  color: #999;
  border-color: #ccc;
  cursor: not-allowed;
}
.part > textarea {
  min-height: 400px;
}