@import url('https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@20..48,100..700,0..1,-50..200');

h1 {
  text-align: center;
  font-size: 2.5rem;
  margin: 1rem 0;
  color: #333;
}

.courses-base{
  display: flex;
  flex-direction: row;
}
.courses-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 1.5rem;
  justify-content: center;
  box-sizing: border-box;
  margin: 0 auto;
    padding: 50px 0 0;
    max-width: 960px;
    width: 100%;
}
.courses-manual{
  flex-basis: 40%;
  padding: 1rem;
  background-color: #FFF1DB;
}
.course-item {
  flex: 1 1 calc(33.333% - 1.5rem);
  box-sizing: border-box;
  padding: 0.5rem;
  border-radius: 8px;
  cursor: pointer;
  transition: box-shadow 0.3s, transform 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #fff;
  overflow: hidden;
  border: 1px solid #ddd;
  flex-shrink: 0;
  flex-grow: 0!important;
}

.image-container {
  position: relative;
  width: 100%;
  height: auto;
}

.course-type {
  position: absolute;
  top: 8px;
  right: 8px;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.9em;
  z-index: 1;
}

.course-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  transition: transform 0.3s, box-shadow 0.3s;
}

.course-item h2 {
  margin: 8px 0 0;
  font-size: 1.2em;
  text-align: center;
  color: #333;
}

.course-item:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transform: translateY(-5px);
}

.course-image:hover {
  transform: scale(1.05);
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px, rgb(51, 51, 51) 0px 0px 0px 3px;
}

.clsz {
  position: absolute;
  top: 8px;
  right: 8px;
  border: none;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 50%;
  padding: 0.3rem;
  cursor: pointer;
  transition: transform 0.3s;
}

.clsz:hover {
  transform: scale(1.1);
  background: rgba(255, 255, 255, 0.9);
}

.overlay-grid {
  display: grid;
  grid-template-columns: 1fr 2fr;
  background: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 4px 8px;
  position: relative;
  max-width: 800px;
  margin: auto;
}

.detail-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.detail-heading {
  border-radius: 8px 25px 8px 25px;
  padding: 0.5rem;
  background-color: #F8EDED;
  text-align: center;
  border-bottom: thick double black;
  font-size: 1.5rem;
  color: #333;
}

.left-grid {
  background-color: #B43F3F;
  padding: 1rem;
  color: white;
  border-radius: 8px;
}

.right-grid {
  padding: 20px;
  background-color: #FFF1DB;
  color: black;
  border-radius: 8px;
  text-shadow: 1px 2px 2px rgba(0, 0, 0, 0.4);
  opacity: 0.9;
}

.wide {
  grid-column: 1 / -1;
  text-align: center;
}

@media (max-width: 800px) {
  .courses-base{
    flex-direction: column;
  }
}