}

.crown{
  display:none;
  width: 48px
}

/* Tablets (768px and up) */
@media (max-width: 1024px) {
  .hero-grid{
    justify-content: space-between;
  }
  .hero {
    grid-template-columns: 1fr;
    height: 80vh;
  }
  .crown{
    display:block;
    width: 48px
  }
  .action{
    position: absolute;
    transform: translateY(38vh);
  }
  .hero-title {
    font-size: 9vw; /* Slightly larger for impact */
    text-align: center;
    font-weight: 900;
    color: coralred; /* Coral red for a striking title */
    text-transform: uppercase; /* Strong, modern appearance */
    letter-spacing: 0.05em; /* Slightly increased for clarity */
    margin-bottom: 1rem;
    text-shadow: 3px 3px 0 rgba(255, 255, 255, 0.6), /* Light shadow for depth */
                 -3px -3px 0 rgba(0, 0, 0, 0.1); /* Subtle inner shadow */
    animation: fadeInUp 1s ease-out; /* Smooth entrance animation */
}

.hero-subtitle {
  position: absolute;
  transform: translateY(50vh);
  width: 90%; /* Keeping the original width */
  text-align: center; /* Maintains original alignment */
  font-family: 'Merriweather', serif;
  font-weight: 800;
  color: black; /* Primary color */
  font-size: clamp(1.5rem, 5vw, 3rem); /* Keeping original scaling */
  line-height: 0.8em; /* Improved readability */
  letter-spacing: 0.03em; /* Polished look */
}

/* Animations */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeInLeft {
    from {
        opacity: 0;
        transform: translateX(-20px);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}



