:root {
  --primary-bg: #000;
  --primary-text: #fff;
  --secondary-bg: #fcd535;
  --secondary-text: #181a20;
  --hover-bg: rgba(299, 299, 299, 0.15);
  --active-bg: red;
  --border-color: rgba(44, 44, 44, 1);
}
/* Basic styles for the CurrentLesson page */
p{
  background: none
}
.wrapper {
  display: grid;
  grid-template-columns: 1fr 2fr;
  column-gap: 20px;
  justify-content: center;
  box-sizing: border-box;
  padding-left: 10vw;
  padding-right: 10vw;
  align-items: center;
}

.wrapper-component {
  display: flex;
  height: 100%;
  flex-direction: column;
  align-items: center;
  gap: 0.2rem;
  flex-grow: 1;
  box-sizing: border-box;
}
.right{
  align-items: stretch;
  background-color: var(--primary-bg);
  padding:1rem;
  color: white;
  border-radius: 8px;

}
.right {
  --background: linear-gradient(to left, #f7ba2b 0%, #ea5358 100%);
  width: 650px;
  margin:auto;
  padding: 5px;
  border-radius: 1rem;
  overflow: visible;
  background: #f7ba2b;
  background: var(--background);
  position: relative;
  z-index: 1;
 }
 
 .right::after {
  position: absolute;
  content: "";
  top: 30px;
  left: 0;
  right: 0;
  z-index: -1;
  height: 100%;
  width: 100%;
  transform: scale(0.8);
  filter: blur(25px);
  background: #f7ba2b;
  background: var(--background);
  transition: opacity .5s;
 }
 
 .right-info {
  padding: 1rem;
  --color: #181818;
  background: var(--color);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  overflow: visible;
  border-radius: .7rem;
 }
 
 .right .title {
  font-weight: bold;
  letter-spacing: .1em;
 }
 
 /*Hover*/
 .right:hover::after {
  opacity: 0;
 }
 
 .right:hover .right-info {
  color: #f7ba2b;
  transition: color 1s;
 }
 

h2 {
  --text-size-base: 14px;
}

.card {
  background-color: silver;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 250px;
  height: 250px;
  margin: 1rem;
  padding: 1rem;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.card:hover {
  background-color: #e0e0e0;
  transform: scale(1.05);
}

.card h3 {
  margin: 0;
  color: white;
  text-shadow:
  -1px -1px 0 #000,
   0   -1px 0 #000,
   1px -1px 0 #000,
   1px  0   0 #000,
   1px  1px 0 #000,
   0    1px 0 #000,
  -1px  1px 0 #000,
  -1px  0   0 #000;
}
.top{
  align-content: first baseline;
}
.card p {
  font-size: 1rem;
  color: #555;
}

/* Error message styling */
.error-message {
  color: #ff0000;
  font-size: 1.2rem;
  text-align: center;
  margin-top: 20px;
}

.contentList {
  margin-top: -8px;
  display: grid;
  width: 100%;
  grid-template-columns: repeat(1, minmax(0px, 1fr));
}

.listImg {
  height: 55px;
  width: 55px;
  border-radius: 8px;
  margin-right: 2rem
}
.mod-info{
  display: flex;
  flex-direction: column
}
.module-num{
  color:#fcd535;
  font-size: 12px;
  padding:0;
  margin:0;
  text-shadow:
  -1px -1px 0 #000,
   0   -1px 0 #000,
   1px -1px 0 #000,
   1px  0   0 #000,
   1px  1px 0 #000,
   0    1px 0 #000,
  -1px  1px 0 #000,
  -1px  0   0 #000;
}
.card-item {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  padding: 10px 0;
  margin:0
}
.card-item:hover {
  background-color: #e0e0e020;
  transform: scale(1.02);
  padding-left: 0.2rem
}
.card-item + div {
  border-top: 1px solid rgba(255, 255, 255, 0.577);
}
.cardItemInside {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
}

.module-name {
  padding: 0;
  margin: 0;
}
.right-cont{
  text-align: center;
  margin:0
}

/* Styling for .correct and .incorrect */
.correct {
  background-color: lightgreen !important;
}
.incorrect {
  background-color: crimson !important;
}

.warming {
  width: 100%;
  padding: 20px;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
  margin: 20px auto;
  position: relative;
  overflow: hidden;
}
.textcontainer {
  width: 100%;
  text-align:justify;
  margin: 0 auto;
  padding: 20px;
  background:#fff;
  margin:20px auto;
  border-radius:4px;
  
  -moz-box-shadow: 0px 0px 28px rgba(0,0,0,.3);
  -webkit-box-shadow: 0px 0px 28px rgba(0,0,0,.3);
   box-shadow: 0px 0px 28px rgba(0,0,0,.3);
   overflow:hidden;
}
.textcontainer > div {
  position: relative;
  width: 100%;
}
/* Central part (mane) - using multi-column layout for two-column magazine style */
.mane {
  column-count: 2; /* Create two equal-width columns */
  column-gap: 30px; /* Gap between columns */
  padding: 20px;
  height: 100%;
  box-sizing: border-box;
  overflow-y: auto; /* Allow scrolling if content overflows */
  margin-bottom: 5rem;
}

/* Styling for the individual elements inside .mane */
.mane > * {
  background-color: white;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 20px; /* Space between elements */
}
.mane img{
  padding: 0.3rem;
  margin: 0.5rem;
  max-width: 100%;
  object-fit: contain; /* Ensure image fits within column */
  border-radius: 8px; 
  box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
    }

.content {
  /* Ensure the content occupies the full width */
  width: 100%;
}

.navigationL {
  grid-column: 1;
  padding: 0.2rem;
  cursor: pointer;

}
.navigationR {
  grid-column: 3;
  cursor: pointer;
  padding: 0.2rem;
}
.disabled{
    cursor: not-allowed;
    opacity: 0.6;
    transition: all 0.3s ease;
    
}

.disabled:hover {
    color: #aaa;
    border-color: #ccc;
}
.navIcon-pos{
  position: fixed;
  transform: translateY(30vh);
  width: 4%;
  opacity: 0.3;
}
.nav-dir{
  text-align:center;
  font-size: 0.8em;
  font-weight: 900;
  color: whitesmoke;
  text-shadow:
  -1px -1px 0 #000,
   0   -1px 0 #000,
   1px -1px 0 #000,
   1px  0   0 #000,
   1px  1px 0 #000,
   0    1px 0 #000,
  -1px  1px 0 #000,
  -1px  0   0 #000;
}
.navigation:disabled {
  background: #cf0a0a;
  cursor: not-allowed;
}

.lesson-page h1{
  padding:0;
  margin: 0;
}

.current-lesson {
  padding: 20px;
}

.next-lesson {
  cursor: pointer;
  border: 1px solid #ccc;
  padding: 10px;
  margin: 10px 0;
}

.lesson-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}
.lesson-overview{
  padding-bottom: 5rem;
}
.lesson-content {
  background: #fff;
