  padding: 20px;
  border-radius: 8px;
  max-width: 800px;
  width: 100%;
  max-height: 90%;
  overflow-y: auto;
}

.close-button {
  align-self: flex-end;
  top: 10px;
  right: 10px;
  background: #f00;
  color: #fff;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
}

.part {
  margin-bottom: 20px;
}

.part h1 {
  font-size: 24px;
}

.part h2 {
  font-size: 20px;
}

div{
  box-sizing: border-box;
}
.modules-list{
  width:100%;

}
.lesson-page .container {
  max-width: 100%;
  text-align:justify;
  margin: 0 auto;
  padding:15px;
  background:#fff;
  margin: 18px auto;
  border-radius:4px;
  position:relative;
  -moz-box-shadow: 0px 0px 28px rgba(0,0,0,.3);
